@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.main {
  background-color: hsl(181, 70%, 29%);
  color: white;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.timer {
  padding: 1rem;
}

.display-and-control-timer {
  border-radius: 10px;
  padding: 1rem 0rem 1.3rem 0rem;
  margin: 1rem;
  border: 4px solid white;
  background-color: hsl(0, 0%, 25%);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
}

@media(min-width: 600px) {
  .timer {
    max-width: 400px;
  }
}

.title {
  font-size: 2.5rem;
  padding: 1rem
}

#timer-label {
  font-size: 1.5rem;
  margin-bottom: 0;
}

#time-left {
  font-size: 2.5rem;
  letter-spacing: 10px;
  padding: 1rem
}

.config-timer {
  padding: 1rem 1rem;
}

.col {
  text-align: center;
}

label {
  font-size: 1.5rem;
}

button {
  margin: 0 10px;
  padding: 5px 15px;
  border: 2px solid white;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: hsl(0, 0%, 25%);
  color: white;
}

button:hover {
  background-color: hsl(0, 0%, 15%);
}

.control-timer-btn {
  color: white;
  background-color: #167D7F;
  padding: 5px 20px;
  margin: 0 5px;
}

.control-timer-btn:hover {
  background-color: white;
  color: hsl(181, 70%, 29%);
}

.author {
  padding-top: 2rem;
  letter-spacing: 1px;
}
